<template>
  <DocItem :title="'Alert '" :testButtons="testButtons" :param="param" @on-test="onTest">
    <div slot="tip">
      
    </div>

    <template slot="example">
      <b-alert v-model="modelData.show" v-bind="modelData" @on-close="onClose">
        <a
          v-if="!modelData.title && !modelData.subTitle"
          slot="content"
          href="https://github.com/qiaodaima/vue-base-ui">
            
        </a>
      </b-alert>
    </template>
  </DocItem>
</template>

<script>
  import DocItem from './doc-item.vue';
  import bAlert from '../src/components/alert/main.vue';

  export default {
    name: 'DocAlert',
    components: {
      DocItem,
      bAlert
    },
    data() {
      return {
        param: [
          {
            name: 'v-model',
            explain: '  ',
            type: 'Boolean',
            default: false,
            isMust: false
          },
          {
            name: 'duration',
            explain: '30',
            type: 'Number',
            default: 3000,
            isMust: false
          },
          {
            name: 'icon',
            explain: '',
            type: 'String',
            default: 'icon-error',
            isMust: false
          },
          {
            name: 'theme',
            explain: ' infosuccesswarningerror',
            type: 'String',
            default: 'info',
            isMust: false
          },
          {
            name: 'title',
            explain: '',
            type: 'String',
            default: '\'\'',
            isMust: false
          },
          {
            name: 'subTitle',
            explain: '',
            type: 'String',
            default: '\'\'',
            isMust: false
          },
          {
            name: 'slot="content"',
            explain: 'title subTitle',
            type: '-',
            default: '-',
            isMust: '-'
          },
          {
            name: 'on-close',
            explain: 'duration 0 ',
            type: '-',
            default: '-',
            isMust: '-'
          }
        ],
        testButtons: [
          {
            flag: 'info',
            text: ''
          },
          {
            flag: 'success',
            text: ''
          },
          {
            flag: 'warning',
            text: ''
          },
          {
            flag: 'error',
            text: ''
          }
        ],
        infoData: {
          show: true,
          theme: 'info',
          icon: 'icon-busy',
          title: '',
          subTitle: ''
        },
        successData: {
          show: true,
          theme: 'success',
          icon: 'icon-success',
          title: '',
          subTitle: 'AppleGoogle'
        },
        warningData: {
          show: true,
          theme: 'warning',
          icon: 'icon-busy',
          title: '',
          duration: 0,
          subTitle: 'bug'
        },
        errorData: {
          show: true,
          theme: 'error',
          icon: 'icon-error',
          title: '',
          subTitle: ''
        },
        modelData: {
          show: false
        }
      };
    },
    methods: {
      onTest(button) {
        switch (button.flag) {
          case 'info':
            this.modelData = JSON.parse(JSON.stringify(this.infoData));
            break;
          case 'success':
            this.modelData = JSON.parse(JSON.stringify(this.successData));
            break;
          case 'warning':
            this.modelData = JSON.parse(JSON.stringify(this.warningData));
            break;
          case 'error':
            this.modelData = JSON.parse(JSON.stringify(this.errorData));
            break;
          default:
            break;
        }
      },
      onClose() {
        alert('');
      }
    }
  };
</script>

<style lang="scss" scoped>
  .alert-item {
    a {
      text-decoration: underline;
    }
  }
</style>
