<template>
  <DocItem :title="'Select '" :param="param">
    <div slot="tip">
      
    </div>

    <template slot="example">
      <dl class="group">
        <dt></dt>
        <dd>
          <b-select v-model="selectedFavoriteFood" :data="favoriteFood"></b-select>
        </dd>
      </dl>
    </template>
  </DocItem>
</template>

<script>
  import DocItem from './doc-item.vue';
  import bSelect from '../src/components/select/main';

  export default {
    name: 'DocSelect',
    components: {
      DocItem,
      bSelect
    },
    data() {
      return {
        selectedFavoriteFood: [],
        favoriteFood: [],
        param: [
          {
            name: 'v-model',
            explain: 'ps:',
            type: 'Array',
            default: [],
            isMust: false
          },
          {
            name: 'data',
            explain: '',
            type: 'Array',
            default: '[{ "key": "", "text": "", "disabled": false }]',
            isMust: true
          },
          {
            name: 'writeable',
            explain: 'ps: ',
            type: 'Boolean',
            default: false,
            isMust: false
          },
          {
            name: 'searchable',
            explain: '',
            type: 'Boolean',
            default: false,
            isMust: false
          },
          {
            name: 'multiple',
            explain: '',
            type: 'Boolean',
            default: false,
            isMust: false
          },
          {
            name: 'disabled',
            explain: '',
            type: 'Boolean',
            default: false,
            isMust: false
          },
          {
            name: 'placeholder',
            explain: '',
            type: 'String',
            default: '',
            isMust: false
          }
        ]
      };
    },
    mounted() {
      this.createData();
    },
    methods: {
      createData() {
        let foods = [
          '',
          '',
          '',
          '',
          '',
          '',
          '',
          '',
          '',
          '',
          ''
        ];
        let total = 10;

        for (let i = 0; i < total; i++) {
          this.favoriteFood.push({
            key: i,
            text: `${foods[Math.floor(Math.random() * 10)]} - ${i}`,
            disabled: i % 4 === 0 ? true : false
          });
        }
        for (let i = 0; i < total; i++) {
          if (i % 3 === 0) {
            this.selectedFavoriteFood.push(i);
          }
        }
      }
    }
  };
</script>

<style lang="scss" scoped>
  .group {
    width: 300px;
  }
</style>
