<template>
  <DocItem :title="'ProgressCircle '" :param="param">
    <div slot="tip">
      100%(#333)100%full-color<br>
      0% --> 100%0%100% text.font<br>
      text.showfalse <br>
      2 setIntervalpercent
      speed3<br>
      <br>
    </div>

    <template slot="example">
      <b-progress-circle
        :size="140"
        :percent="percent"
        @click.native="percentIncrease">
      </b-progress-circle>

      <b-progress-circle
        :size="140"
        :percent="75"
        :trail-color="'transparent'"
        :stroke-color="'#ff5500'"
        :text="{show: false}"
        :speed="0">
        <p slot="content">
          <span style="font-size: 18px;">85%</span>
        </p>
      </b-progress-circle>

      <b-progress-circle
        :size="140"
        :percent="percent"
        :trail-color="'transparent'"
        :stroke-color="'#ff5500'"
        :text="{show: false}"
        @click.native="percentIncrease">
        <p slot="content">
          <span v-if="percentTemp !== 100" style="font-size: 18px;">{{percentTemp}}%</span>
          <span v-else style="font-size: 20px; color: #5cb85c">ok</span>
        </p>
      </b-progress-circle>

      <b-progress-circle
        :size="140"
        :percent="percent"
        :text="{show: false}"
        :full-color="'#ff5e5e'"
        @click.native="percentIncrease">
        <div slot="content" style="color: #888;">
          <small></small>
          <p>{{percent}}%</p>
        </div>
      </b-progress-circle>
    </template>
  </DocItem>
</template>

<script>
  import DocItem from './doc-item.vue';
  import bProgressCircle from '../src/components/progress-circle/main.vue';

  export default {
    name: 'DocProgressCircle',
    components: {
      DocItem,
      bProgressCircle
    },
    data() {
      return {
        param: [
          {
            name: 'size',
            explain: 'px',
            type: 'Number',
            default: 120,
            isMust: false
          },
          {
            name: 'percent',
            explain: '%0-1000100',
            type: 'Number',
            default: 0,
            isMust: false
          },
          {
            name: 'width',
            explain: 'px',
            type: 'Number',
            default: 5,
            isMust: false
          },
          {
            name: 'trail-color',
            explain: '',
            type: 'String',
            default: '#eaeef2',
            isMust: false
          },
          {
            name: 'stroke-color',
            explain: '(100%)',
            type: 'String',
            default: '#2db7f5',
            isMust: false
          },
          {
            name: 'full-color',
            explain: '100%',
            type: 'String',
            default: '#5cb85c',
            isMust: false
          },
          {
            name: 'text',
            explain: '',
            type: 'Object',
            default: {
              show: true,
              font: 'normal 18px Microsoft YaHei'
            },
            isMust: false
          },
          {
            name: 'speed',
            explain: '0',
            type: 'Number',
            default: 15,
            isMust: false
          },
          {
            name: 'slot="content"',
            explain: '',
            type: '-',
            default: '-',
            isMust: '-'
          }
        ],
        percent: 80,
        percentTemp: 0
      };
    },
    mounted() {
      this.percentTempFn();
    },
    methods: {
      percentIncrease() {
        this.percent = this.percent + 10 > 100 ? 0 : this.percent + 10;
        this.percentTempFn();
      },
      percentTempFn() {
        let _this = this;
        let timer = null;

        _this.percentTemp = 0;
        timer = setInterval(function() {
          _this.percentTemp >= _this.percent ? clearInterval(timer) : _this.percentTemp++;
        }, 15);
      }
    }
  };
</script>

<style lang="scss" scoped>
  .progress-circle-item {
    margin-left: 60px;

    &:first-child {
      margin-left: 0;
    }
  }
</style>
