<template>
  <label class="switch-item">
    <input
      v-model="isChecked"
      :disabled="disabled"
      @change="onChange"
      type="checkbox">
    <span class="inner-text">{{this.value ? this.text[0] : this.text[1]}}</span>
  </label>
</template>

<script>
  export default {
    name: 'bSwitch',
    props: {
      value: {
        type: Boolean,
        required: true
      },
      disabled: {
        type: Boolean,
        default: false
      },
      text: {
        type: Array,
        default: function() {
          return ['', ''];
        }
      }
    },
    computed: {
      isChecked: {
        get() {
          return this.value;
        },
        set(newValue) {
          this.$emit('input', newValue);
        }
      }
    },
    methods: {
      onChange() {
        this.$emit('on-change', event);
      }
    }
  };
</script>

<style lang="scss" scoped>
  .switch-item {
    $height: 22px; //  
    $margin: 2px; // 

    position: relative;
    cursor: pointer;

    input[type="checkbox"] {
      z-index: 10;
      position: absolute;
      left: 0; top: 0;
      width: 100%; height: 100%;
      opacity: 0;

      &:checked {
        & ~ .inner-text {
          padding-left: 8px;
          padding-right: $height - $margin * 2 + 5px; //  5px
          text-align: left;
          background: #409eff;

          // css   right
          &:after {
            left: calc(100% - 2px);
            transform: translate(-100%, 0);
          }
        }
      }
      &:disabled {
        & ~ .inner-text {
          opacity: .5;
        }
      }
    }
    .inner-text {
      position: relative;
      padding-left: $height - $margin * 2 + 5px;
      padding-right: 8px;
      min-width: 45px; height: $height;
      line-height: $height;
      color: #fff;
      text-align: right;
      font-size: 12px;
      border-radius: $height;
      background: #c0c4cc;
      transition: all .2s;

      &:after {
        position: absolute; content: "";
        left: $margin; top: $margin; bottom: $margin;
        width: $height - $margin * 2; //     
        border-radius: 50%;
        background: #fff;
        transition: all .2s;
      }
    }
  }
</style>
