<template>
  <DocItem :title="'Transfer '" :param="param">
    <div slot="tip">
      <br>
      on-changedirectionselect: key<br>
      data: (abled)(disabled)(all)
    </div>

    <template slot="example">
      <p>{{modelData.select}}</p>

      <b-transfer
        v-model="modelData.select"
        v-bind="modelData"
        @on-change="onChange">
      </b-transfer>
    </template>
  </DocItem>
</template>

<script>
  import DocItem from './doc-item.vue';
  import bTransfer from '../src/components/transfer/main.vue';

  export default {
    name: 'DocTransfer',
    components: {
      DocItem,
      bTransfer
    },
    data() {
      return {
        param: [
          {
            name: 'v-model',
            explain: 'datakey',
            type: 'Array',
            default: '[] key',
            isMust: true
          },
          {
            name: 'data',
            explain: '',
            type: 'Array',
            default: [
              {
                key: '',
                text: '',
                disabled: false
              }
            ],
            isMust: true
          },
          {
            name: 'titles',
            explain: '',
            type: 'Array',
            default: ['', ''],
            isMust: false
          },
          {
            name: 'on-change',
            explain: '',
            type: '-',
            default: '-',
            isMust: '-'
          }
        ],
        modelData: {
          data: [],
          select: []
        }
      };
    },
    mounted() {
      this.createData();
    },
    methods: {
      createData() {
        let subject = [
          '',
          '',
          '',
          '',
          '',
          '',
          '',
          '',
          '',
          ''
        ];
        let teacher = [
          '',
          '',
          '',
          '',
          '',
          '',
          '',
          '',
          '',
          ''
        ];
        let total = 25;

        for (let i = 0; i < total; i++) {
          this.modelData.data.push({
            key: i,
            text: `${subject[Math.floor(Math.random() * 10)]} - ${teacher[Math.floor(Math.random() * 10)]} - ${i}`,
            disabled: i % 4 === 0 ? true : false
          });
        }
        for (let i = 0; i < total; i++) {
          if (i % 3 === 0) {
            this.modelData.select.push(i);
          }
        }
      },
      onChange(result) {
        console.log(result);
      }
    }
  };
</script>

