<template>
  <DocItem :title="'Loading '" :testButtons="testButtons" :param="param" @on-test="onTest">
    <div slot="tip">
      
    </div>

    <template slot="example">
      <b-loading v-model="modelData.show" v-bind="modelData"></b-loading>
    </template>
  </DocItem>
</template>

<script>
  import DocItem from './doc-item.vue';
  import bLoading from '../src/components/loading/main.vue';

  export default {
    name: 'DocLoading',
    components: {
      DocItem,
      bLoading
    },
    data() {
      return {
        param: [
          {
            name: 'v-model',
            explain: '  ',
            type: 'Boolean',
            default: false,
            isMust: false
          },
          {
            name: 'icon',
            explain: '',
            type: 'String',
            default: 'icon-loading',
            isMust: false
          },
          {
            name: 'text',
            explain: '',
            type: 'String',
            default: '',
            isMust: false
          }
        ],
        testButtons: [
          {
            flag: 'showLoading',
            text: ''
          }
        ],
        modelData: {
          show: false
        }
      };
    },
    methods: {
      showLoading() {
        this.modelData.show = true;

        setTimeout(function() {
          this.modelData.show = false;
        }.bind(this), 3000);
      },
      onTest(button) {
        switch (button.flag) {
          case 'showLoading':
            this.showLoading();
            break;
          default:
            break;
        }
      }
    }
  };
</script>

